.outer {
  float: right;
  max-width: 1840px;
  padding: 0 30px;
  width: 100%; }

.inner {
  padding-left: 77px;
  width: 100%; }

.section {
  &__outer {
    height: 100%; }
  &__inner {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px; }
  &__left {
    @include flex-size(27.5%);
    padding: 0 88px 0 15px; }
  &__right {
    @include flex-size(72.5%);
    padding: 0 15px;
    position: relative; }
  &__bg {
    @include coverdiv;
    @include cover; }
  .fp-tableCell {
    vertical-align: top;
    height: 100% !important; } }
